body {
  font: 300 16px /1.5em 'Lato', sans-serif;
}

/*links*/
a {
  color: #a29061;
  text-decoration: none;
}

a:hover{
  opacity: 0.8;
}

/*images*/
img{
  max-width: 100%;
  height: auto;
}

/*grid*/
.container{
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.container-header{
  max-width: 1800px;
}
/*Page Header*/
.page-header{
  padding:55px 0;
}

.page-header > .container{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.brand .brand img{
  display: block;
}

/*Menu*/
.menu-items{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.menu-items li.menu::before{
  position:absolute;
  content:'';
  height:4.3rem;
  border-left: 1.1px solid;
  margin-top:-4rem;
  margin-left:.6rem;
  color: #2d3439;
  opacity: 0.1;
}

.menu-items li.active::before{
  position:absolute;
  content:'';
  height:4.3rem;
  border-left: 1px solid;
  margin-top:-4rem;
  margin-left:.6rem;
  color: #4d565d;
  opacity: 1;
}

.menu-items li{
  margin: 0 4px;
  display: inline-block;
  list-style: none;
}

.menu-items li a{
  padding: 10px 10px;
  display: inline-block;
  font-size: 18px;
  line-height: 1.2em;
  font-weight: 400;
  letter-spacing: 0.05px;
}

.menu-items .active{
  color: #4d565d;
}

.menu-items .menu-icon a{
  background-repeat: no-repeat;
  padding: 0;
  width: 70px;
  height: 13px;
}

.menu-line{
  background-image: url('../img/menu line.svg');

}
/*Top Section*/
.top-section{
  background-image: url('../img/top-bg.png');
  background-size: cover;
  height:1150px;
  width: auto;
}

.section-header {
  color: #af9a63;
  font-size: 1.5em;
  font-weight: 400;
  line-height: 2.5em;
}

.section-header .active{
  border-bottom: 2px solid;
  color: #af9a63;
  width:417px;
}

.section-header .passive{
  opacity: 0.5;
}

.top-section-title{
  text-transform: uppercase;
  margin-top: -100px;
  margin-left: 75px;
}

.top-section-title h1{
  color: #455560;
  font-size: 5em;
  font-weight: 300;
  line-height: 1.4em;
  z-index:10;
}

.top-section-title h2 {
  color: #a29061;
  font-size: 3.75em;
  line-height: 1.4em;
  font-weight: 300;
}

.arrow{
  margin-top:350px;
  display: flex;
  justify-content: flex-end;
}

/*welcome-section*/
.welcome-section{
  background-image: url('../img/welcome bg grey.png');
  background-size: contain;
  background-repeat: no-repeat;
  height:1000px;
  width: auto;
  position:relative;
  color:#ffffff;
}

.welcome-section .container{
  display:flex;
}

.welcome-section .content{
  margin-top: 150px;
  width: 45%;
}

.content h2{
  font-size: 3.75em;
  text-transform:uppercase;
  font-weight: 300;
}

.content h4{
  font-size: 2.5em;
  text-transform:uppercase;
  font-weight: 300;
  color: #a29061;
}

.content p {
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0.16px;
}

.first-btn{
  background-color: transparent;
  border: none;
  position:relative;
  margin-top: 610px;
  margin-right:100px;
}

/*news-section*/
.news-section {
  background-image: url('../img/news bg.png');
  background-size: cover;
  height: 800px;
  width: auto;
  margin-top: -300px;
}

.news-section .container{
  display: flex;
  justify-content: flex-end;
}

.news-section-content{
  width: 37%;
  margin-top: 160px;
  margin-right: 200px;
  text-align: justify;
}

.news-section-content h2{
  color: #4d565d;
  font-size: 3.75em;
  font-weight: 300;
  text-transform: uppercase;
  border-bottom: 2px solid;
  width: 100%;
  line-height: 1em;
}

.news-section-content span{
  color: #a29061;
  font-size: 1.4em;
  font-weight: 300;
  text-transform: uppercase;
  border-bottom: 1px solid;
  width: 100%;
  opacity:0.9;
}

.news-section-content h4 {
  color: #4d565d;
  font-size: 2em;
  font-weight: 300;
  text-transform: uppercase;
  line-height: 1em;
  letter-spacing: normal;
  word-spacing: 0.5em;
  text-align: left;
}

.news-section-content p{
  color: #4d565d;
  font-weight: 400;
  letter-spacing: 0.16px;
}
.news-section-button{
  display: flex;
  justify-content: flex-end;
}
.news-section-btn{
  border: 1px solid  #af9a63;
  border-radius: 50%;
  background-color: #af9a63;
  font-size: 2em;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  width: 219px;
  height: 219px;
}

/*page footer*/
.page-footer .links{
  background-color:#455560;
}

.links .container {
  display:flex;
  padding: 50px 0;
  box-sizing: border-box;

}

.menu-footer{
  list-style: none;
  display: inline-block;

}

.menu-footer a{
  color: #7c888f;
  text-transform: uppercase;
}

.line-separator{
  height:65px;
  width:1.1px;
  background:#57666f;
  border-bottom:1px solid #57666f;
}

.page-footer-form{
  margin-left: 30%;
  margin-top:3%;
  display: flex;

}

.page-footer-form .text{
  font: 300 16px /1.5em 'Lato', sans-serif;
  color: #57666f;
  font-size: 2em;
  text-transform: uppercase;
  padding: 0 50px;
  margin-top: 15px;
}

.buttons{
  display: flex;
}

.buttons input{
  font: 300 16px /1.5em 'Lato', sans-serif;
  color: #57666f;
  font-size: 1.5em;
}
.button_email {
  height:65px;
  width: 296px;
  border: 1px solid #2d3439;
  background-color: #455560;
  box-sizing: border-box;
}

.button_subscribe{
  height:65px;
  width: 244px;
  background-color: #2d3439;
  color:#ffffff;
  font-size:1.5em;
  text-transform: uppercase;
  margin-left: -20px;
  border: 2px solid #2d3439;
  box-sizing: border-box;
}

.site-footer__bottom {
  background-color: #2d3439;
  color: #575d61;
}

.site-footer__bottom .container{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.site-footer__bottom .contact{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.contact a{
  color: #575d61;
}

.contact img{
  margin-right:20px;
}






